body {
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

.breed-select {
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 24px;
}

.cat-info {
  align-items: center;
  margin: 0 auto;
  padding: 20px 15px;
  display: flex;
}

.cat_photo {
  margin-right: 40px;
}

.descr_container {
  max-width: 500px;
  border: 4px solid gray;
  border-radius: 15%;
  padding: 40px;
}

.cat_name {
  font-size: 48px;
  line-height: 1.15;
}

.cat_descr, .cat_temperament {
  font-size: 26px;
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

